document.addEventListener("DOMContentLoaded", function () {
    var pregnancyClick = 0;
    const button = document.getElementById("pregnancy");
    const buttonRmv = document.getElementById("pregnancyRmv");
    var abortClick = 0;
    const abortButton = document.getElementById("abort");
    const abortButtonRmv = document.getElementById("abortRmv");
    const pregnancyLimit = 6;
    const abortLimit = 3;

    button.addEventListener("click", function (e) {
        e.preventDefault();
        if (pregnancyClick < pregnancyLimit) {
            const pregnancySection = document.querySelectorAll(".pregancySection");
            pregnancySection.forEach(function (element, index) {
                // console.log(`Element ${index + 1}:`, element);
                if (index == pregnancyClick) {
                    element.classList.remove("display-none");
                }
                if (pregnancyClick >= 0) {
                    const removeBtn = document.getElementById("pregnancyRmv");
                    removeBtn.classList.remove("display-none");
                }
                // // Example: Change the text content of each element
                // element.textContent = `Updated Element ${index + 1}`;
            });
            pregnancyClick++;
            document.getElementById("pregnancyNum").value = pregnancyClick;
        }
    });

    buttonRmv.addEventListener("click", function (e) {
        e.preventDefault();
        if (pregnancyClick <= pregnancyLimit) {
            pregnancyClick--;
            document.getElementById("pregnancyNum").value = pregnancyClick;
            const pregnancySection = document.querySelectorAll(".pregancySection");
            pregnancySection.forEach(function (element, index) {
                // console.log(`Element ${index + 1}:`, element);
                if (index == pregnancyClick) {
                    element.classList.add("display-none");
                }
                if (pregnancyClick <= 0) {
                    const removeBtn = document.getElementById("pregnancyRmv");
                    removeBtn.classList.add("display-none");
                }
                // // Example: Change the text content of each element
                // element.textContent = `Updated Element ${index + 1}`;
            });
        }
    });

    abortButton.addEventListener("click", function (e) {
        e.preventDefault();
        if (abortClick < abortLimit) {
            const abortSection = document.querySelectorAll(".abortSection");
            abortSection.forEach(function (element, index) {
                // console.log(`Element ${index + 1}:`, element);
                if (index == abortClick) {
                    element.classList.remove("display-none");
                }
                if (abortClick >= 0) {
                    const removeBtn = document.getElementById("abortRmv");
                    removeBtn.classList.remove("display-none");
                }
                // // Example: Change the text content of each element
                // element.textContent = `Updated Element ${index + 1}`;
            });
            abortClick++;
        }
    });

    abortButtonRmv.addEventListener("click", function (e) {
        e.preventDefault();
        if (abortClick <= abortLimit) {
            abortClick--;
            const abortSection = document.querySelectorAll(".abortSection");
            abortSection.forEach(function (element, index) {
                // console.log(`Element ${index + 1}:`, element);
                if (index == abortClick) {
                    element.classList.add("display-none");
                }
                if (abortClick <= 0) {
                    const removeBtn = document.getElementById("abortRmv");
                    removeBtn.classList.add("display-none");
                }
                // // Example: Change the text content of each element
                // element.textContent = `Updated Element ${index + 1}`;
            });
        }
    });
});

function abortEnable() {
    const enableMsg = document.getElementById("enableAbort").value;
    // const abort1 = document.getElementById("abortEnable1");
    const abort2 = document.getElementById("abortEnable2");
    if (enableMsg == "yes") {
        // abort1.classList.remove("display-none");
        abort2.classList.remove("display-none");
    } else {
        // abort1.classList.add("display-none");
        abort2.classList.add("display-none");
    }
}

function formClick() {
    var dateValue = document.getElementById("form_date").value;
    var dateObject = new Date(dateValue);
    var year = dateObject.getUTCFullYear();
    var month = dateObject.getUTCMonth() + 1;
    var day = dateObject.getUTCDate() + 1;
    var currentDate = new Date();
    var currentYear = currentDate.getUTCFullYear();
    var currentmonth = currentDate.getUTCMonth() + 1;
    var currentday = currentDate.getUTCDate() + 1;
    console.log("x")
    console.log(day);
    console.log(currentday);
    console.log("y");
    if (year > 1900) {
        if (currentmonth >= month) {
            if (currentday >= day) {
                var age = currentYear - year;
            } else {
                var age = currentYear - year - 1;       
            }
        } else {
            var age = currentYear - year - 1;
        }
        var ageValue = document.getElementById("form_age");
        ageValue.value = age;
    }
}